const CONTRACT_ADDRESS = '0xE1Ef01C19751540778b73c122B4fc9a5EF543ECf';  // Use your contract address

export const connectWallet = async () => {
    if (window.ethereum) {
        web3 = new Web3(window.ethereum);
        await window.ethereum.enable();
        contract = new web3.eth.Contract(ABI, CONTRACT_ADDRESS);
        const accounts = await web3.eth.getAccounts();
        return accounts[0];
    } else {
        console.error("Ethereum provider not detected");
        throw new Error("Ethereum provider not detected");
    }
}

export const getCurrentAccount = async () => {
    const accounts = await web3.eth.getAccounts();
    return accounts[0];
}

// This function will return the contract
export const getContract = () => {
    if (!contract) {
        throw new Error("Contract not initialized. Ensure connectWallet is called first.");
    }
    return contract;
}

